.songRow{
    margin-left: 20px;
    padding: 20px;
    display: flex;
    align-items:center;
    color: white;
}
.songRow:hover {
    cursor:pointer;
    background-color:black;
    opacity:0.8;
}

.songrowinfo{
    margin-left: 20px;
}

.songrowinfo > h1{
    font-size: 16px;
}

.songrowinfo > p{
    font-size: 14px;
    margin-top:3px;
    color:gray;
}
.songRowImg{
    height:40px;
    width: 40px;
}