.header
{
    display:flex;
    justify-content:space-between;
    margin-bottom:30px;
    align-items:center;
}
.header_left{
    flex:0.5;
    min-width:70px;
    background-color:white;
    color:gray;
    border-radius:30px;
    display:flex;
    align-items:center;
    padding:10px;
}
.header_left > input{
    border:none;
    width:100%;
}
.header_right{
    display:flex;
    align-items:center;
   
}
.header_right > h4{
    margin-left: 10px;
}
